.contact-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
}

.form-container,
.contact-details {
  width: 50%;
}

.form-container {
  margin-right: 20px;
}

.contact-details {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
}

.contact-details h2 {
  margin-bottom: 10px;
  font-size: 20px;
}

.contact-details p {
  margin-bottom: 5px;
}

.contact-details strong {
  font-weight: bold;
}

.contact-form {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
}

.contact-form h2 {
  margin-bottom: 10px;
  font-size: 20px;
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

textarea {
  height: 150px;
}

button[type="submit"] {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}
